import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

export interface SidebarItem {
  id: string;
  value: string;
  label: string;
  checked: boolean;
  color: string;
}

interface SidebarProps {
  items: SidebarItem[];
  footerText: string;
  onItemCheckedChange: (id: string, checked: boolean) => void
}

interface StyledCheckboxProps {
  color: string;
}

const StyledCheckbox = styled.label<StyledCheckboxProps>`
  /* your existing styles go here */
  --checkbox-color: ${props => props.color};

  &::before {
    background-color: "#fff";
  }

  input:checked + &::before {
    /* styles for checked state here */
    background-color: var(--checkbox-color);
  }
  input:not(:checked) + &::before {
    /* styles for unchecked state here */
    background-color: "#fff";
  }
`;


const Sidebar: React.FC<SidebarProps> = ({ items, onItemCheckedChange, footerText  }) => {
  const [sidebarItems, setSidebarItems] = useState<SidebarItem[]>(items);

  // This effect updates sidebarItems when props.items changes
  useEffect(() => {
    setSidebarItems(items);
  }, [items]);
  
  const handleCheckboxChange = (event: React.ChangeEvent<HTMLInputElement>, item: SidebarItem) => {
    const newSidebarItems = sidebarItems.map(v => 
      v.id === item.id ? { ...v, checked: !v.checked } : v
    );
    setSidebarItems(newSidebarItems);

    const updatedItem = newSidebarItems.find(v => v.id === item.id);
    if (updatedItem) {
      onItemCheckedChange(item.id, updatedItem.checked);
    }
  }
  const handle_all_change = (event: React.ChangeEvent<HTMLInputElement>) => {
    let view_all = event.target.checked;

    const newSidebarItems = sidebarItems.map(v => {return { ...v, checked: view_all }} );
    setSidebarItems(newSidebarItems);
    
    items.forEach(v => {
      onItemCheckedChange(v.id, view_all)
    })
  }

  return (
    <aside className="sidebar">
      <div className="sidebar-item">
        <input className="checkbox-all" type="checkbox" id="all" value="all" onChange={handle_all_change} defaultChecked />
        <label className="checkbox checkbox-all" htmlFor="all">View all</label>
      </div>
      <hr />
      {sidebarItems.map((item: SidebarItem, i) => (
        <div className="sidebar-item" key={item.id}>
          <input 
            type="checkbox" 
            id={item.id} 
            value={item.value} 
            checked={item.checked}  
            onChange={event => handleCheckboxChange(event, item)}
            className="hidden-checkbox"
          />
          <StyledCheckbox 
            htmlFor={item.id}
            color={item.color}
            className={`checkbox ${item.checked ? "checked" : ""} checkbox-calendar checkbox-${i}`}
          >
            {item.label}
          </StyledCheckbox>
        </div>
      ))}
      <hr />
      <div className="app-footer">{footerText}</div>
    </aside>
  );
};
export default Sidebar;