import React from 'react';
import type { ChangeEvent } from 'react';

type ViewType = 'month' | 'week' | 'day';

const viewModeOptions = [
  {
    title: 'Monthly',
    value: 'month',
  },
  {
    title: 'Weekly',
    value: 'week',
  },
  {
    title: 'Daily',
    value: 'day',
  },
];


type SelectViewState = {
    selectedView: ViewType;
};

type SelectViewProps = {
    initialView: ViewType;
    onChange: (value: ViewType) => void;
};
  
export default class SelectView extends React.Component<SelectViewProps, SelectViewState> {
    constructor(props: SelectViewProps) {
      super(props);
      this.state = {
        selectedView: props.initialView,
      };
    }
  
    handleChange = (ev: ChangeEvent<HTMLSelectElement>) => {
      const value = ev.target.value as ViewType;
      this.setState({ selectedView: value }, () => {
        this.props.onChange(value);
      });
    };
  
    render() {
      return (
        <div className="button is-rounded">
          <select
            style={{ borderColor: "rgb(10 10 10 / 0%)", background: "transparent", WebkitAppearance: "none"
        }}
            onChange={this.handleChange}
            value={this.state.selectedView}
          >
            {viewModeOptions.map((option, index) => (
              <option label={option.title} value={option.value} key={index}></option>
            ))}
          </select>
        </div>
      );
    }
  }
  