import './App.css';
import '@toast-ui/calendar/dist/toastui-calendar.min.css';

import type { ExternalEventTypes } from '@toast-ui/calendar';
import type { MouseEvent } from 'react';
import { useCallback, useEffect, useRef, useState } from 'react';

import Calendar from '@toast-ui/react-calendar';
import { theme } from './theme';
import Sidebar from './SideBar';
import { addOrUpdateEvent, calendarSideBarItems, deleteEventById, loadedEvents, loadedProfiles } from './data';
import { TopBar } from './TopBar';
import SelectView from './SelectView';

type ViewType = 'month' | 'week' | 'day';

export function DaisyCalendar({ view }: { view: ViewType }) {
  const calendarRef = useRef<typeof Calendar>(null);
  const [selectedDateRangeText, setSelectedDateRangeText] = useState('');
  const [selectedView, setSelectedView] = useState(view);

  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const getCalInstance = useCallback(() => calendarRef.current?.getInstance?.(), []);

  const calendarVisibility = (id: string, checked: boolean) => {
      getCalInstance().setCalendarVisibility(id, checked);
      console.log("CHnage",id, checked)
    }

  const updateRenderRangeText = useCallback(() => {
    const calInstance = getCalInstance();
    if (!calInstance) {
      setSelectedDateRangeText('');
    }

    const viewName = calInstance.getViewName();
    const calDate = calInstance.getDate();
    const rangeStart = calInstance.getDateRangeStart();
    const rangeEnd = calInstance.getDateRangeEnd();

    let year = calDate.getFullYear();
    let month = calDate.getMonth() + 1;
    let date = calDate.getDate();
    let dateRangeText: string;

    switch (viewName) {
      case 'month': {
        dateRangeText = `${year}-${month}`;
        break;
      }
      case 'week': {
        year = rangeStart.getFullYear();
        month = rangeStart.getMonth() + 1;
        date = rangeStart.getDate();
        const endMonth = rangeEnd.getMonth() + 1;
        const endDate = rangeEnd.getDate();

        const start = `${year}-${month < 10 ? '0' : ''}${month}-${date < 10 ? '0' : ''}${date}`;
        const end = `${year}-${endMonth < 10 ? '0' : ''}${endMonth}-${
          endDate < 10 ? '0' : ''
        }${endDate}`;
        dateRangeText = `${start} ~ ${end}`;
        break;
      }
      default:
        dateRangeText = `${year}-${month}-${date}`;
    }

    setSelectedDateRangeText(dateRangeText);
  }, [getCalInstance]);

  useEffect(() => {
    setSelectedView(view);
  }, [view]);

  useEffect(() => {
    updateRenderRangeText();
  }, [selectedView, updateRenderRangeText]);

  const onAfterRenderEvent: ExternalEventTypes['afterRenderEvent'] = (res) => {
    console.group('onAfterRenderEvent');
    console.log('Event Info : ', res.title);
    console.groupEnd();
  };

  const onBeforeDeleteEvent: ExternalEventTypes['beforeDeleteEvent'] = (res) => {
    console.group('onBeforeDeleteEvent');
    console.log('Event Info : ', res.title);
    console.groupEnd();

    const { id, calendarId } = res;
    deleteEventById(id)
    getCalInstance().deleteEvent(id, calendarId);
  };

  const onClickDayName: ExternalEventTypes['clickDayName'] = (res) => {
    console.group('onClickDayName');
    console.log('Date : ', res.date);
    console.groupEnd();
  };

  const onClickNavi = (ev: MouseEvent<HTMLButtonElement>) => {
    if ((ev.target as HTMLButtonElement).tagName === 'BUTTON') {
      const button = ev.target as HTMLButtonElement;
      const actionName = (button.getAttribute('data-action') ?? 'month').replace('move-', '');
      getCalInstance()[actionName]();
      updateRenderRangeText();
    }
  };

  const onClickEvent: ExternalEventTypes['clickEvent'] = (res) => {
    console.group('onClickEvent');
    console.log('MouseEvent : ', res.nativeEvent);
    console.log('Event Info : ', res.event);
    console.groupEnd();
  };

  const onClickTimezonesCollapseBtn: ExternalEventTypes['clickTimezonesCollapseBtn'] = (
    timezoneCollapsed
  ) => {
    console.group('onClickTimezonesCollapseBtn');
    console.log('Is Timezone Collapsed?: ', timezoneCollapsed);
    console.groupEnd();

    const newTheme = {
      'week.daygridLeft.width': '100px',
      'week.timegridLeft.width': '100px',
    };

    getCalInstance().setTheme(newTheme);
  };

  const onBeforeUpdateEvent: ExternalEventTypes['beforeUpdateEvent'] = (updateData) => {
    console.group('onBeforeUpdateEvent');
    console.log(updateData);
    console.groupEnd();

    const targetEvent = updateData.event;
    const changes = { ...updateData.changes };
    getCalInstance().updateEvent(targetEvent.id, targetEvent.calendarId, changes);
    
    const newEvent = {...targetEvent, ...updateData.changes}
    addOrUpdateEvent({
      id: newEvent.id,
      calendarId: newEvent.calendarId,
      title: newEvent.title || "",
      start: new Date(newEvent.start?.toString()!) || Date.now(),
      end: new Date(newEvent.end?.toString()!) || Date.now(),
    })
  };

  const onBeforeCreateEvent: ExternalEventTypes['beforeCreateEvent'] = (eventData) => {
    const event = {
      calendarId: eventData.calendarId || '',
      id: String(Math.random()),
      title: eventData.title,
      isAllday: eventData.isAllday,
      start: eventData.start,
      end: eventData.end,
      category: eventData.isAllday ? 'allday' : 'time',
      dueDateClass: '',
      location: eventData.location,
      state: eventData.state,
      isPrivate: eventData.isPrivate,
    };

    getCalInstance().createEvents([event]);
    addOrUpdateEvent({
      id: event.id,
      calendarId: event.calendarId,
      title: event.title || "",
      start: new Date(event.start?.toString()!) || Date.now(),
      end: new Date(event.end?.toString()!) || Date.now(),
    })
  };

  let sidebar = Sidebar({
    footerText: '© Daisy Care Co.',
    items: calendarSideBarItems,
    onItemCheckedChange: calendarVisibility
  })
  let topbar = TopBar({callback: onClickNavi})

  return (
    <div className="app-container code-html">
    <header style={{height: "7vh"}} className="header">
      <h1 style={{fontSize: "1.5em"}}>🌼📅 Daisy Care Co.</h1>
    </header>
    <article className="content">
      {sidebar}
      <section className="app-column">

    <div>
      <div style={{height: "5vh"}}>
      <SelectView 
          initialView={selectedView} 
          onChange={(v: ViewType) => {setSelectedView(v)}}
        />
        {topbar}
        <span className="render-range">{selectedDateRangeText}</span>
      </div>
      <Calendar
        height="88vh"
        calendars={loadedProfiles}
        events={loadedEvents}
        month={{ startDayOfWeek: 1 }}
        template={{
          milestone(event) {
            return `<span style="color: #fff; background-color: ${event.backgroundColor};">${event.title}</span>`;
          },
          allday(event) {
            return `[All day] ${event.title}`;
          },
        }}
        theme={theme}
        timezone={{
          zones: [
            {
              timezoneName: 'Australia/Brisbane',
              displayLabel: 'BNE',
              tooltip: 'UTC+010:00',
            }
          ],
        }}
        useDetailPopup={true}
        useFormPopup={true}
        view={selectedView}
        week={{
          startDayOfWeek: 1,
          narrowWeekend: true,
          showTimezoneCollapseButton: true,
          timezonesCollapsed: false,
          eventView: true,
          taskView: false,
          hourStart: 5,
          hourEnd: 22,
        }}
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        ref={calendarRef}
        onAfterRenderEvent={onAfterRenderEvent}
        onBeforeDeleteEvent={onBeforeDeleteEvent}
        onClickDayname={onClickDayName}
        onClickEvent={onClickEvent}
        onClickTimezonesCollapseBtn={onClickTimezonesCollapseBtn}
        onBeforeUpdateEvent={onBeforeUpdateEvent}
        onBeforeCreateEvent={onBeforeCreateEvent}
      />
    </div>
    </section>
      </article>
    </div>
  );
}
