function saveToLocalStorage(key: string, data: any) {
  try {
    localStorage.setItem(key, JSON.stringify(data));
  } catch (err) {
    console.error(`Error saving to localStorage: ${err}`);
  }
}

function loadFromLocalStorage<T>(key: string, defaultValue: T): T {
  try {
    let data = localStorage.getItem(key)
    if (data === null) {
      console.log("Data is null, saving defaultValue to localStorage.");
      saveToLocalStorage(key, defaultValue);
      return defaultValue;
    } else {
      console.log("Data found, parsing data.");
      return JSON.parse(data || "") as T;
    }
  } catch (err) {
    console.error(`Error loading from localStorage: ${err}`);
    return defaultValue;
  }
}
function deleteFromLocalStorage(key: string): void {
  try {
    localStorage.removeItem(key);
  } catch (err) {
    console.error(`Error deleting from localStorage: ${err}`);
  }
}

export function addOrUpdateEvent(event: IEvent): void {
  try {
    // Load the current array of events from localStorage
    let events = loadFromLocalStorage<IEvent[]>('events', []);

    // Check if an event with the same id already exists
    const existingEventIndex = events.findIndex(e => e.id === event.id);

    if (existingEventIndex > -1) {
      // If an event with the same id exists, update it
      events[existingEventIndex] = event;
    } else {
      // If no event with the same id exists, add the new event
      events.push(event);
    }

    // Save the updated array of events back to localStorage
    saveToLocalStorage('events', events);
  } catch (err) {
    console.error(`Error adding or updating event in localStorage: ${err}`);
  }
}

export function deleteEventById(id: string): void {
  try {
    // Load the current array of events from localStorage
    let events = loadFromLocalStorage<IEvent[]>('events', []);

    // Filter out the event with the given id
    const updatedEvents = events.filter(event => event.id !== id);

    // Save the updated array of events back to localStorage
    saveToLocalStorage('events', updatedEvents);
  } catch (err) {
    console.error(`Error deleting event from localStorage: ${err}`);
  }
}

