import React from 'react';
import type { MouseEvent } from 'react';

type TopProps = {
    callback: (ev: MouseEvent<HTMLButtonElement>) => void
  }
  
  interface ButtonProps {
    action: string;
    label: string;
  }
  
  const buttons: ButtonProps[] = [
    { action: "move-today", label: "Today" },
    { action: "move-prev", label: "Prev" },
    { action: "move-next", label: "Next" },
  ];
  
  export const TopBar: React.FC<TopProps> = ({callback}) => {
    return (
      <span>
        {buttons.map(({ action, label }) => (
          <button
            key={action}
            type="button"
            className="button is-rounded btn btn-default btn-sm move-day"
            data-action={action}
            onClick={callback}>
            {label}
          </button>
        ))}
      </span>
    );
}
  